    methods: {
      setTimeNow () {
        var date = new Date()
        var day = date.getDate()
        var month = date.getMonth() + 1
        var year = date.getFullYear()
        if (month < 10) {
          month = '0' + month
        }
        if (day < 10) {
          day = '0' + day
        }
        this.now = day + '/' + month + '/' + year
        const m = [
          'JANEIRO',
          'FEVEREIRO',
          'MARÇO',
          'ABRIL',
          'MAIO',
          'JUNHO',
          'JULHO',
          'AGOSTO',
          'SETEMBRO',
          'OUTUBRO',
          'NOVEMBRO',
          'DEZEMBRO',
        ]
        this.monthInWords = m[date.getMonth()]
        if (this.editedIndex === -1) {
          this.editedItem.DATA = this.now
          this.editedItem.MÊS = this.monthInWords
        }
      },
      refreshEveryFive () {
        setTimeout(() => {
          if (this.dialog === false  && this.dialogSave === false && this.dialogDelete === false) {
            location.reload()
          } else {return}
        }, 300000)
      },
      statusRules () {
        var completeDate = new Date()
        var day = completeDate.getDate()
        var month = completeDate.getMonth() + 1
        var year = completeDate.getFullYear()
        if (month < 10) {
          month = '0' + month
        }
        if (day < 10) {
          day = '0' + day
        }
        var today = year + '' + month + '' + day
        var vencimentoDate = this.editedItem.VENCIMENTOBOLETO
        const [d, m, y] = vencimentoDate.split('/')
        var realDate = `${y}${m.padStart(2, '0')}${d.padStart(2, '0')}`

        console.log('today: ' + today)
        console.log('venc boleto: ' + realDate)
        if (realDate < today) {
          this.editedItem.STATUS = 'VENCIDO'
        }
      },

